import { RootState } from '../index'; // Root Vuex Store  RootState 
import router from '@/router';
import api from '@/api/api';
import axios from 'axios';

interface RefrigeratorState {
    ingredients: Ingredient[];
    seasonings: Seasoning[];
}

interface Ingredient {
    pk : number
    name: string
    amount: number
    unit: string
    startDate: string
    endDate: string
    storage: string
}

interface Seasoning {
    pk : number
    name: string
    startDate: string
    endDate: string
    storage: string
}




const refrigerator: Module<RefrigeratorState, RootState> = {
    state: {
        ingredients : [{pk: 0, name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''}, 
            {pk: 1,name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 2,name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 3,name : "", amount: 600, unit: "g",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 4,name : "", amount: 1200, unit: "g",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 5,name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''}, 
            {pk: 6,name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 7,name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 8,name : "", amount: 600, unit: "g",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 9,name : "", amount: 1200, unit: "g",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 10,name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''}, 
            {pk: 11,name : "", amount: 2, unit: "",  startDate : '2023-07-27', endDate: '', storage: ''},],
        seasonings : [{pk: 0, name : "", startDate : '2023-07-27', endDate: '', storage: ''}, 
            {pk: 1, name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 2,name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 3,name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 4,name : "", startDate : '2023-07-27', endDate: '', storage: ''}, 
            {pk: 5,name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 6,name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 7,name : "", startDate : '2023-07-27', endDate: '', storage: ''}, 
            {pk: 8,name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 9,name : "",  startDate : '2023-07-27', endDate: '', storage: ''},
            {pk: 10,name : "",  startDate : '2023-07-27', endDate: '', storage: ''},],
    },
    getters: {
        ingredients: state => state.ingredients,
        seasonings: state => state.seasonings
    },
    mutations: {
        PLUS_AMOUNT: (state, tempIngredient) => {state.ingredients = tempIngredient}
            
    },
    actions: {
        plusAmount({commit, getters}, ingredient) {
            const tempIngredient = getters.ingredients
            console.log(tempIngredient)
            for (let i: number = 0; i < tempIngredient.length; i++) {
                if(ingredient.pk === tempIngredient[i].pk) {
                    if(tempIngredient[i].unit === "g") {
                        tempIngredient[i].amount += 100
                    }
                    else {
                        tempIngredient[i].amount ++
                    }
                    
                }
            }
            console.log(tempIngredient)
            commit("PLUS_AMOUNT", tempIngredient)
        }
            
        }
    }


export default refrigerator
